@font-face {
	font-family: 'Ferry';
	src: url('./assets/fonts/Ferry-Black/Ferry-Black.eot');
	src: local('Ferry Black'), local('Ferry-Black'),
		url('./assets/fonts/Ferry-Black/Ferry-Black.eot?#iefix') format('embedded-opentype'),
		url('./assets/fonts/Ferry-Black/Ferry-Black.woff') format('woff'),
		url('./assets/fonts/Ferry-Black/Ferry-Black.ttf') format('truetype');
	font-weight: 900;
	font-style: normal;
}

/* Reset and base styles  */
* {
    box-sizing: border-box;
	padding: 0px;
	margin: 0px;
	border: none;
}

*,
*::before,
*::after {
	box-sizing: border-box;
}

/* Links */

a, a:link, a:visited  {
    text-decoration: none;
}

a:hover  {
    text-decoration: none;
}

/* Common */

aside, nav, footer, header, section, main {
	display: block;
}

h1, h2, h3, h4, h5, h6, p {
    font-size: inherit;
	font-weight: inherit;
}

ul, ul li {
	list-style: none;
}

img {
	vertical-align: top;
}

img, svg {
	max-width: 100%;
	height: auto;
}

address {
  font-style: normal;
}

/* Form */
input, textarea, button, select {
	font-family: inherit;
    font-size: inherit;
    color: inherit;
    background-color: transparent;
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type='number'] {
  --moz-appearance: textfield;
}

input::-ms-clear {
	display: none;
}

button, input[type="submit"] {
    display: inline-block;
    box-shadow: none;
    background-color: transparent;
    background: none;
    cursor: pointer;
}

input:focus, input:active,
button:focus, button:active, 
textarea:active, textarea:focus,
select:focus {
    outline: none;
}

button::-moz-focus-inner {
	padding: 0;
	border: 0;
}

label {
	cursor: pointer;
}

legend {
	display: block;
}
/* End Reset and base styles */

:root {
    --bg-color: #5F8671;
    --nero: #292627;
    --white: #FFFFFF;
    --light-yellow: #FFCB74;
    --red: rgb(250, 51, 51);
}

body {
    font-family: 'Nunito', sans-serif;
    font-size: 16px;
	font-weight: 500;
    background-color: var(--bg-color);
    position: relative;
    min-height: 100vh;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 15px;
}